@import url(https://fonts.googleapis.com/css2?family=Roboto+Mono&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Oswald&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lexend:wght@600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Titillium+Web:wght@200&display=swap);
html {
  height: 100%;
  overflow: hidden;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100%;
}
:root {
  --screenHeight: 66.6667%
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
#root {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  height: 100%;
  min-width: calc(100vh * 1.25);
}
.DesktopBG {
  position: fixed;
  height: 100%;
}
.LockScreenCon {
  position: relative;
  height: 66.6667%;
  height: var(--screenHeight);
  width: calc(66.6667vh * (16/9));
  z-index: 10;
  margin-top: 0.65%;
  overflow: hidden;
}
.LockScreenMover {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  width: 100%;
  height: 100%;
  position: relative;
  margin-top: -110%;
  -webkit-animation: LockScreenRollUp 12.5s ease-in-out 0s 1;
          animation: LockScreenRollUp 12.5s ease-in-out 0s 1;
}
@-webkit-keyframes LockScreenRollUp {
  0% {margin-top: 0%;}
  90% {margin-top: 0%;}
  100% {margin-top: -110%;}
}
@keyframes LockScreenRollUp {
  0% {margin-top: 0%;}
  90% {margin-top: 0%;}
  100% {margin-top: -110%;}
}
.LockScreenBG {
  position: absolute;
  height: 100%;
  z-index: 10;
}
.Hello {
  position: absolute;
  height: 12%;
  z-index: 20;
  margin-top: 0.5%;
}
.HelloText {
  position: absolute;
  height: 36% !important;
  z-index: 20;
  margin-top: 8%;
}
.WindowsDesktop {
  width: 100%;
  position: absolute;
  bottom: 0;
}
.VSCodeWindow {
  width: 50%;
  height: 100%;
  position: absolute;
  bottom: 0;
  background-color: #222222;
  box-shadow: 0px 0px 6px 0px rgba(0,0,0,0.5);
}
.VSCodeHeader {
  width: 100%;
  height: 3%;
  position: absolute;
  top: 0;
  background-color: #424242;
}
.CloseButton {
  width: 6%;
  height: 100%;
  position: absolute;
  right: 0;
  background-color: #ff3a3a;
}
.VSCodeSidebar {
  width: 15%;
  height: 100%;
  position: absolute;
  left: 0;
  background-color: #303030;
}
.VSCodeText {
  margin-top: 3%;
  height: 48%;
  width: 81%;
  right: 0;
  position: absolute;
  color: #ffffff;
  font-size: 1.5vh;
  line-height: 1.5vh;
  padding: 2%;
  font-family: 'Roboto Mono', sans-serif;
}
span.purple{
  color: #c586b5;
}
span.blue{
  color: #9cdcfe;
}
span.darkblue{
  color: #569cd6;
}
span.green{
  color: #4ebe7d;
}
span.orange{
  color: #ce9178;
}
span.yellow{
  color: #dcdcaa;
}
.VSCodeTerminal {
  margin-top: 55%;
  width: 85%;
  height: 45%;
  right: 0;
  position: absolute;
  font-family: 'Roboto Mono', sans-serif;
  border-top: 1px grey solid;
  box-sizing: border-box;
}
.VSCodeTerminal label {
  color: #ffffff;
  line-height: 1.6vh;
  font-size: 1.6vh;
  margin-left: 2%;
}
.VSCodeTerminal input {
  width: 98%;
  border: none;
  box-sizing: border-box;
  background-color: #222222;
  color: #ffffff;
  font-family: 'Roboto Mono', sans-serif;
  line-height: 1.6vh;
  font-size: 1.6vh;
  margin-left: 2%;
}
textarea:focus, input:focus{
  outline: none;
}
.NotepadWindow {
  width: 20%;
  height: 30%;
  position: absolute;
  top: 20%;
  right: 15%;
  background-color: #eeca0a;
  box-shadow: 0px 0px 6px 0px rgba(0,0,0,0.5);
}
.NotepadHeader {
  width: 100%;
  height: 12%;
  position: absolute;
  top: 0;
  background-color: #f0ae03;
}
.NotepadText {
  font-family: "Comic Sans MS", "Comic Sans", cursive;
  font-size: 1.6vh;
  line-height: 1.7vh;
  padding: 2%;
  margin-top: 12%;
}
.NotepadHeader .CloseButton {
  width: 15%;
}
.FirefoxWindow {
  position: absolute;
  bottom: 0;
  margin: auto;
  background-color: #ffffff;
}
.FirefoxOpen-enter {
  width: 0%;
  height: 0%;
}
.FirefoxOpen-enter-active {
  width: 100%;
  height: 100%;
  transition: width 150ms ease-in, height 150ms ease-in;
}
.FirefoxOpen-enter-done {
  width: 100%;
  height: 100%;
}
.FirefoxHeaderUpper {
  width: 100%;
  height: 8%;
  position: relative;
  top: 0;
  background-color: black;
}
.FirefoxHeaderLower {
  width: 100%;
  height: 66%;
  position: absolute;
  bottom: 0;
  background-color: #424242;
}
.FirefoxHeaderUpper .CloseButton {
  width: 3%;
}
.FirefoxPage {
  width: 100%;
  height: 86%;
  position: relative;
  overflow-y: scroll;
}
.PortfolioItem {
  width: 100%;
  height: 45%;
  position: relative;
  cursor: pointer;
}
.ScamCheck {
  background-color: #ffffff;
}
.StatsExpress {
  background-color: #0b102b;
}
.RLPlaza {
  background-color: #0b102b;
}
.ImageCon {
  width: 50%;
  height: 100%;
  right: 0;
  position: absolute;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
}
.LogoCon {
  width: 50%;
  height: 100%;
  left: 0;
  position: absolute;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}
.ScamCheck .LogoCon, .RLPlaza .LogoCon {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}
.LogoCon img {
  height: 2.6em;
  padding-right: 0.7em;
}
.SETitle1 {
  font-family: 'Oswald', sans-serif;
  color: #eee;
  font-size: 2em;
  line-height: 1em;
  letter-spacing: 0.02em;
  margin: 0;
  font-weight: bold;
}
.SETitle2 {
  font-family: 'Oswald', sans-serif;
  color: #ff4655;
  font-size: 2.5em;
  line-height: 1.2em;
  letter-spacing: 0.12em;
  margin: 0;
  font-weight: bold;
}
.StatsExpressImg {
  height: 180%;
}
.ScamCheckImg {
  height: 100%;
  position: absolute;
  right: 0;
  bottom: 0;
}
.RLPlazaImg {
  height: 100%;
  overflow: hidden;
}
.SCTitle {
  font-family: 'Lexend', sans-serif;
  font-size: 2em;
  color: #214aff;
}
.RLTitle {
  font-family: 'Titillium Web', sans-serif;
  font-size: 2em;
  color: #00c1ce;
}
.TaskBar {
  width: 100%;
  height: 6%;
  position: absolute;
  bottom: 0;
  background-color: black;
}
.TaskBarItem {
  display: inline-block;
  height: 100%;
  width: 3.6%;
  position: relative;
}
.TaskBarImg {
  height: 60%;
  padding: 20%;
  position: absolute;
}
.TaskBar form {
  width: 25.2%;
  height: 100%;
  display: inline-block;
  position: relative;
}
.TaskBar form input {
  width: 100%;
  height: 100%;
  background-color: #1d1d1d;
  color: #ffffff;
  padding-left: 5%;
  position: absolute;
  border: 1px grey solid;
  box-sizing: border-box;
  font-size: 1.6vh;
}

